import React, { useState, useEffect } from "react";
import WeatherAlertForm from "../components/WeatherAlert";
import { weatherApi } from "../services/api";
import { WeatherAlert } from "../types";
import { Trash2, MapPin } from "lucide-react";

const Alerts: React.FC = () => {
  const [alerts, setAlerts] = useState<WeatherAlert[]>([]);

  useEffect(() => {
    loadAlerts();
  }, []);

  const loadAlerts = async () => {
    try {
      const data = await weatherApi.getAlerts();
      setAlerts(data);
    } catch (err) {
      console.error(err);
    }
  };

  const handleDelete = async (id: string) => {
    if (!window.confirm("Delete this alert?")) return;
    try {
      await weatherApi.deleteAlert(id);
      await loadAlerts();
    } catch (err) {
      console.error(err);
    }
  };

  return (
    <div className="container mx-auto px-4 py-6 grid md:grid-cols-2 gap-8">
      {/* Create Alert */}
      <div className="bg-white dark:bg-gray-800 rounded-2xl shadow-lg p-6 hover:shadow-xl transition animate-fadeIn">
        <h2 className="text-xl font-bold mb-4 text-secondary">
          âž• Create New Alert
        </h2>
        <WeatherAlertForm children={loadAlerts} />
      </div>

      {/* Existing Alerts */}
      <div className="bg-white dark:bg-gray-800 rounded-2xl shadow-lg p-6 hover:shadow-xl transition animate-fadeIn">
        <h2 className="text-xl font-bold mb-4 text-gray-800 dark:text-gray-200">
          ðŸ“‹ Existing Alerts
        </h2>
        {alerts.length === 0 ? (
          <p className="text-gray-500 dark:text-gray-400 text-center">
            No alerts yet
          </p>
        ) : (
          <div className="space-y-4">
            {alerts.map((a) => (
              <div
                key={a.id}
                className="bg-gray-50 dark:bg-gray-700 p-4 rounded-xl shadow hover:shadow-md transition"
              >
                <h3 className="font-bold text-lg">
                  {a.name || "Unnamed Alert"}
                </h3>
                <p className="flex items-center gap-2 text-gray-600 dark:text-gray-300">
                  <MapPin className="w-5 h-5" />
                  {a.locationType === "city" ? a.city : `${a.lat},${a.lon}`}
                </p>
                <p className="mt-1 text-gray-700 dark:text-gray-300">
                  {a.parameter} {a.operator} {a.threshold}
                </p>
                <button
                  onClick={() => a.id && handleDelete(a.id)}
                  className="mt-3 flex items-center gap-2 bg-danger hover:bg-red-600 text-white px-3 py-1 rounded transition"
                >
                  <Trash2 size={14} /> Delete
                </button>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default Alerts;
