import React, { useState } from "react";
import { weatherApi } from "../services/api";

const WeatherAlertForm: React.FC = () => {
  const [formData, setFormData] = useState<{
    name: string;
    locationType: "city" | "coords";
    city: string;
    parameter: "temperature" | "windSpeed" | "precipitation";
    operator: ">";
    threshold: number;
    description: string;
  }>({
    name: "",
    locationType: "city",
    city: "",
    parameter: "temperature",
    operator: ">",
    threshold: 0,
    description: "",
  });

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      await weatherApi.createAlert(formData);
      // Reset form
      setFormData({
        name: "",
        locationType: "city",
        city: "",
        parameter: "temperature",
        operator: ">",
        threshold: 0,
        description: "",
      });
    } catch (error) {
      console.error("Failed to create alert:", error);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div>
        <label className="block mb-2">Alert Name:</label>
        <input
          type="text"
          value={formData.name}
          onChange={(e) => setFormData({ ...formData, name: e.target.value })}
          className="w-full p-2 border rounded"
        />
      </div>

      <div>
        <label className="block mb-2">Location Type:</label>
        <select
          value={formData.locationType}
          onChange={(e) =>
            setFormData({
              ...formData,
              locationType: e.target.value as "city" | "coords",
            })
          }
          className="w-full p-2 border rounded"
        >
          <option value="city">City</option>
          <option value="coords">Coordinates</option>
        </select>
      </div>

      {formData.locationType === "city" ? (
        <div>
          <label className="block mb-2">City:</label>
          <input
            type="text"
            value={formData.city}
            onChange={(e) => setFormData({ ...formData, city: e.target.value })}
            className="w-full p-2 border rounded"
          />
        </div>
      ) : null}

      <div>
        <label className="block mb-2">Parameter:</label>
        <select
          value={formData.parameter}
          onChange={(e) =>
            setFormData({
              ...formData,
              parameter: e.target.value as
                | "temperature"
                | "windSpeed"
                | "precipitation",
            })
          }
          className="w-full p-2 border rounded"
        >
          <option value="temperature">Temperature</option>
          <option value="windSpeed">Wind Speed</option>
          <option value="precipitation">Precipitation</option>
        </select>
      </div>

      <button
        type="submit"
        className="bg-blue-500 text-white px-4 py-2 rounded"
      >
        Create Alert
      </button>
    </form>
  );
};

export default WeatherAlertForm;
