import React, { useState, useEffect } from "react";
import { weatherApi } from "../services/api";
import { WeatherData } from "../types";

const Home: React.FC = () => {
  const [location, setLocation] = useState<string>("Tel Aviv");
  const [weather, setWeather] = useState<WeatherData | null>(null);
  const [loading, setLoading] = useState(false);

  const fetchWeather = async () => {
    setLoading(true);
    try {
      const data = await weatherApi.getRealtimeWeather(location);
      setWeather(data);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchWeather();
  }, []);

  return (
    <div className="container">
      <div className="card max-w-md mx-auto">
        <input
          type="text"
          value={location}
          onChange={(e) => setLocation(e.target.value)}
          placeholder="Enter location..."
          className="input mb-2"
        />
        <button onClick={fetchWeather} className="btn mb-4">
          Update Location
        </button>

        {loading ? (
          <div>Loading...</div>
        ) : weather ? (
          <div className="text-center">
            <h2 className="text-2xl mb-2">{location}</h2>
            <div className="text-4xl font-bold">{weather.temperature}Â°C</div>
            <div className="text-gray-600">
              Feels like: {weather.feels_like}Â°C
            </div>
            <div className="grid grid-cols-2 gap-4 mt-4">
              <div>
                <strong>Wind:</strong> {weather.wind_speed} m/s
              </div>
              <div>
                <strong>Humidity:</strong> {weather.humidity}%
              </div>
            </div>
          </div>
        ) : (
          <div>No weather data available</div>
        )}
      </div>
    </div>
  );
};

export default Home;
